'use strict';

import angular from 'angular';
import 'angular-material';
import mainwrap from 'common/directives/mainwrap/mainwrap';
import featsCtrl from './feats.controller';
import './feats.tpl';
import './feats.header.tpl';
import './feats.css!';

const featsModule = angular.module('feats', [
  mainwrap.name,
  'app/feats/feats.tpl.html',
  'app/feats/feats.header.tpl.html',
  featsCtrl.name
]);

featsModule.config(function($stateProvider){
  $stateProvider.state('feats', {
    url: '/feats',
    templateUrl: 'app/feats/feats.tpl.html',
    controller: 'FeatsCtrl',
    controllerAs: 'featsCtrl',
    authenticate: false
  });
});

export default featsModule;
